.headerDetails {
  margin-top: 6.9rem;
  padding: 2rem;
  width: 100vw;
  height: 40rem;
  background: rgba(0, 0, 0, 0.94);
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  position: relative;
}
.headerDetails--poster {
  height: 100%;
  width: 25%;
}
.headerDetails--poster__img {
  width: 100%;
  height: 100%;
}
.headerDetails--container {
  width: 75%;
  height: 100%;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.headerDetails--container__title {
  font-size: 3.5rem;
  font-weight: bold;
  padding-bottom: 0.5rem;
  border-bottom: 0.7rem solid #d8a860;
  align-self: flex-start;
}
.headerDetails--container__desc {
  font-size: 2.3rem;
  text-align: justify;
  line-height: 2.3rem;
  font-weight: 100;
  padding-top: 2rem;
}
.headerDetails--info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 90%;
  margin-top: 4rem;
}
.badge-decoration {
  width: 20rem;
  height: 3.5rem;
  border: 1px solid #e28c09;
  color: white;
  background: #d8a860;
  position: absolute;
  right: -1.8rem;
  top: 5rem;
  transform: rotateZ(45deg);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2.5rem;
  font-weight: bold;
  overflow: hidden !important;
}

@media all and (max-width: 414px) {
  .headerDetails {
    margin-top: 6.9rem;
    padding: 2rem;
    width: 100vw;
    height: 40rem;
    background: rgba(0, 0, 0, 0.94);
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    position: relative;
  }
  .headerDetails--poster {
    height: 50%;
    width: 25%;
  }
  .headerDetails--poster__img {
    width: 100%;
  }
  .headerDetails--container {
    width: 75%;
    height: 100%;
    padding: 2rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .headerDetails--container__title {
    z-index: 2;
    font-size: 3.5rem;
    font-weight: bold;
    padding-bottom: 0.5rem;
    border-bottom: 0.7rem solid #d8a860;
    align-self: center;
  }
  .headerDetails--container__desc {
    font-size: 1.5rem;
    text-align: center;
    line-height: 2.3rem;
    font-weight: 100;
    padding-top: 2rem;
  }
  .headerDetails--info {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    margin-top: 4rem;
  }
  .badge-decoration {
    width: 20rem;
    height: 3.5rem;
    border: 1px solid #e28c09;
    color: white;
    background: #d8a860;
    position: absolute;
    right: -2rem;
    top: 5rem;
    transform: rotateZ(45deg);
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 2.5rem;
    font-weight: bold;
    overflow: hidden !important;
    z-index: 1;
  }
}
